.login {
  height: 100%;
  width: 100%;
  background-image: url(/static/media/bg.ebab4490.jpg);
  background-size: 100% 100%;
}
.login .login-header {
  display: flex;
  align-items: center;
  height: 80px;
  background-color: #22657d;
}
.login .login-header img {
  height: 40px;
  width: 40px;
  margin: 0 15px 0 50px;
}
.login .login-header h1 {
  color: white;
  font-size: 30px;
  margin: 0;
}
.login .login-content {
  width: 400px;
  height: 300px;
  background-color: rgba(255, 255, 255, 0.5);
  margin: 50px auto;
  padding: 20px;
}
.login .login-content h2 {
  text-align: center;
  font-size: 25px;
  font-weight: bold;
}
.login .login-content .login-form .login-form-button {
  width: 100%;
}
